// ButtonClickHandler.ts
import { _decorator, Component, Node, EventTouch, Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ButtonClickHandler')
export class ButtonClickHandler extends Component {

    onLoad() {
        this.node.on(Node.EventType.TOUCH_START, this.onButtonTouchStart, this);
        this.node.on(Node.EventType.TOUCH_END, this.onButtonTouchEnd, this);
        this.node.on(Node.EventType.TOUCH_CANCEL, this.onButtonTouchCancel, this);
    }

    onButtonTouchStart(event: EventTouch) {
        // 阻止 ScrollView 的触摸事件
        event.propagationStopped = true;
    }

    onButtonTouchEnd(event: EventTouch) {
        // 阻止 ScrollView 的触摸事件
        event.propagationStopped = true;
        // 处理按钮点击逻辑
        console.log('Button clicked');
    }

    onButtonTouchCancel(event: EventTouch) {
        // 阻止 ScrollView 的触摸事件
        event.propagationStopped = true;
    }
}
